@use '_partials/variables' as v
@use '_partials/mixins' as m

// =====================================================================
// TOC:

// II. Styles reset		default.sass
// III. Default styles
//    1. Basic
//    2. Typography
//    3. Links
//    4. Images
//    5. Buttons
//    6. Forms
//    7. Common classes
// =====================================================================

// II. Styles reset

*,
*:before,
*:after
  margin: 0
  padding: 0
  box-sizing: border-box
  -webkit-tap-highlight-color: transparent
  background: none

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video
  border: 0
  outline: 0
  vertical-align: baseline
  background: transparent

address
  font-style: normal

[hidden]
  display: none

button,
input,
textarea
  border: none
  outline: none
  border-radius: 0

code, kbd, pre, samp
  font-family: monospace, serif
  font-size: 1em

i
  display: inline-block

pre
  white-space: pre
  white-space: pre-wrap
  word-wrap: break-word

blockquote,
q
  quotes: &#8220 &#8220
  &:before,
  &:after
    content: ''
    content: none

ul, ol
  list-style: none

table
  border-collapse: collapse
  border-spacing: 0

// III. Default styles
//  1. Basic
// -------------------  

// .continer_xs не нужен - достаточно функций .container
.container
  &,
  &_xxl,
  &_xl,
  &_lg,
  &_md,
  &_sm
    margin: 0 auto
    padding: 0 v.$gutter
    max-width: 1320px
  &_fluid
    padding: 0 v.$gutter
    width: 100%
    max-width: none

.row
  margin: 0 (- v.$gutter)
  display: flex
  flex-wrap: wrap


