//   4. Images
//  -------------------------

img
  display: block
  max-width: 100%
  height: auto

//   5. Buttons
//  -------------------------
// Перенесены в components

button,
input[type="submit"],
input[type="reset"],
input[type="button"],
.btn
  text-decoration: none
  cursor: pointer
  user-select: none

// 6. Forms 
// -------------------------

input:not(input[type="checkbox"]),
select
  -webkit-appearance: none
  -moz-appearance: none
  appearance: none
  outline: none

input::-ms-clear
  display: none

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration
  display: none

select::-ms-expand
  display: none

// 7. Displays
.d
  &-block
    display: block
  &-flex
    display: flex
  &-inline
    display: inline
  &-none
    display: none
